import React, { useState } from 'react';
import * as Yup from 'yup'; // Importe toutes les fonctionnalits du module Yup (bibliothque)
import { yupResolver } from '@hookform/resolvers/yup'; // Importe la fonction yupResolver depuis le module '@hookform/resolvers/yup'
import { useForm } from 'react-hook-form'; // Importation du hook useForm pour grer le formulaire
import axios from 'axios';
// Importe le composant Popup depuis le fichier './Popup' et importe le CSS
import Popup from './Popup';
import './Popup.css';


// Composant ErrorMsg export par dfaut, prenant "projectType,servicesRequests" comme argument.
export default function ErrorMsg({ projectType, servicesRequests }) {
  // Ajoutez un tat pour grer l'affichage de la popup
  const [showPopup, setShowPopup] = useState(false); // Cre 'showPopup'  l'aide de useState et initialise sa valeur  'false'.
  // "Yup.object()" cre un schma de validation. La mthode "shape({})" dfinir les rgles de VALIDATION
  const yupValidation = Yup.object().shape({
    first_name: Yup.string().required('Veuillez entrer votre prnom.').min(4, 'Le prnom doit contenir au moins 4 caractres.'), // mthode Yup indique que la valeur de la proprit "firstName" doit tre une chane de caractres. Si la proprit "firstName" est vide, une erreur sera renvoye avec le message "Veuillez entrer votre prnom." 
    last_name: Yup.string().required('Veuillez entrer votre nom.').min(4, 'Le nom doit contenir au moins 4 caractres.'),
    company_name: Yup.string().required('Veuillez entrer le nom de votre entreprise/organisation.').min(4, 'Le nom de l\'entreprise/organisation doit contenir au moins 4 caractres.'),
    email: Yup.string().required('L\'identifiant de messagerie est obligatoire.')
      .email("Veuillez entrer une adresse e-mail valide."),  // Vrifie si la valeur du champ correspond  un format d'adresse e-mail.
    phone_number: Yup.string().required('Veuillez entrer votre numro de tlphone.').min(10, 'Le numro de tlphone doit contenir au moins 10 caractres.'),
    website_url: Yup.string()
      .required('Veuillez entrer l\'URL de votre site.')
      .url("Veuillez entrer une URL valide."),   // Vrifie si la valeur du champ correspond  un format d'URL
    project_description: Yup.string().required('Veuillez nous parler de votre projet.').min(8, 'La description du projet doit contenir au moins 8 caractres.'),
  });


  const formOptions = { resolver: yupResolver(yupValidation) }; // Dfinit les options du formulaire avec une proprit "resolver" qui utilise la validation Yup.
  const { register, handleSubmit, reset, formState } = useForm(formOptions);  // Utilise la fonction useForm avec les options du formulaire pour obtenir les mthodes. Destructure les proprits register, handleSubmit, reset et formState de l'objet retourn.
  const { errors } = formState; // Destructure la proprit errors de l'objet formState, qui contient les erreurs de validation du formulaire.



  // ENVOI DU FORMULAIRE VIA L'API
  const baseURL = 'http://projetlaravel/api/estimates'; // URL de base
  const method = 'POST'; // Dfinissez la mthode HTTP (POST)

  // fonction pour soumettre le formulaire
  async function onSubmit(data) { // Fonction asynchrone pour grer la soumission du formulaire
    try {
      let response;
      // Utilisation d'une instruction switch pour grer diffrentes mthodes HTTP
      switch (method) {
        case 'POST':
          data.project_type = projectType; // Attribue la valeur de "projectType"  la proprit "project_type" de l'objet "data"
          data.services_requests = servicesRequests.toString(); // Convertit l'objet servicesRequests en une chane de caractres et l'assigne  la variable data.services_requests 
          // Effectue une requte POST vers l'URL de base avec les donnes et un en-tte CSRF
          console.log(data);// Affiche les donnes du formulaire dans la console
          response = await axios.post(baseURL, data, { // Effectue une requte POST vers l'URL de base avec les donnes du formulaire
            headers: {// un en-tte CSRF (Cross-Site Request Forgery) pour renforcer la scurit de la requte
              'X-CSRF-TOKEN': document.querySelector('meta[name="csrf-token"]').getAttribute('content'),
            },
          });
          break; // Sort du switch lorsque la mthode est 'POST'
        case 'PUT':
          response = await axios.put(baseURL, data); // Effectue une requte PUT avec les donnes  l'URL de base
          break; // Sort du switch lorsque la mthode est 'PUT'
        case 'DELETE':
          response = await axios.delete(baseURL, { data }); // Effectue une requte DELETE avec les donnes  l'URL de base
          break; // Sort du switch lorsque la mthode est 'DELETE'
        default:
          console.error('Mthode HTTP non prise en charge'); // Affiche une erreur si la mthode n'est pas reconnue
          return; // Sort de la fonction
      } // Aprs avoir trait la soumission avec succs, rinitialisez le formulaire
      reset(); // Rinitialise le formulaire en utilisant la fonction reset de react-hook-form
      setShowPopup(true); // Pour afficher le popup lorsque showPopup est true
    } 
    catch (error) {
      // Gestion des erreurs en cas d'chec de la soumission (VIDE)
    }
  }


  return (
    <div className="container mt-4">
      <form onSubmit={handleSubmit(onSubmit)}> {/* Lorsqu'il est soumis, la fonction handleSubmit(onSubmit) sera appele. */}
        <div className="form-group" style={{ width: '500px' }}>
          <label>Prnom</label>
          <input // balise <input> Crer un champ de saisie dans un formulaire web.
            name="first_name"
            type="text"
            className={`form-control ${errors.first_name ? 'is-invalid' : ''}`}
              // Dfinit les classes CSS du champ de saisie. 'form-control' est toujours prsente.
              // 'is-invalid' est ajoute si des erreurs existent pour le champ "first_name".  
            {...register('first_name')}  // Lie le champ de saisie au formulaire en utilisant la mthode register 
          />
          <div className="invalid-feedback">{errors.first_name?.message}</div>
        </div>
        <div className="form-group" style={{ width: '500px' }}>
          <label>Nom</label>
          <input
            name="last_name"
            type="text"
            className={`form-control ${errors.last_name ? 'is-invalid' : ''}`}
            {...register('last_name')} // Lie le champ de saisie au formulaire en utilisant la mthode register 
          />
          <div className="invalid-feedback">{errors.last_name?.message}</div>
        </div>
        <div className="form-group" style={{ width: '500px' }}>
          <label>Entreprise / Organisation</label>
          <input
            name="company_name"
            type="text"
            className={`form-control ${errors.company_name ? 'is-invalid' : ''}`}
            {...register('company_name')} // Lie le champ de saisie au formulaire en utilisant la mthode register 
          />
          <div className="invalid-feedback">{errors.company_name?.message}</div>
        </div>
        <div className="form-group" style={{ width: '500px' }}>
          <label>Email</label>
          <input
            name="email"
            type="text"
            className={`form-control ${errors.email ? 'is-invalid' : ''}`}
            {...register('email')} // Lie le champ de saisie au formulaire en utilisant la mthode register 
          />
          <div className="invalid-feedback">{errors.email?.message}</div>
        </div>
        <div className="form-group" style={{ width: '500px' }}>
          <label>Tlphone</label>
          <input
            name="phone_number"
            type="text"
            className={`form-control ${errors.phone_number ? 'is-invalid' : ''}`}
            {...register('phone_number')} // Lie le champ de saisie au formulaire en utilisant la mthode register 
          />
          <div className="invalid-feedback">{errors.phone_number?.message}</div>
        </div>
        <div className="form-group" style={{ width: '500px' }}>
          <label>Site / URL</label>
          <input
            name="website_url"
            type="text"
            className={`form-control ${errors.website_url ? 'is-invalid' : ''}`}
            {...register('website_url')} // Lie le champ de saisie au formulaire en utilisant la mthode register 
          />
          <div className="invalid-feedback">{errors.website_url?.message}</div>
        </div>
        <div className="form-group" style={{ width: '500px' }}>
          <label>Parlez-nous de votre projet</label>
          <textarea
            name="project_description"
            className={`form-control ${errors.project_description ? 'is-invalid' : ''}`} style={{ height: '150px' }}
            {...register('project_description')} // Lie le champ de saisie au formulaire en utilisant la mthode register 
          />
          <div className="invalid-feedback">{errors.project_description?.message}</div>
        </div>
        <div className="mt-3">
          <button type="submit" className="btn btn-primary"> Envoyer </button>
        </div>
        {/* Afficher la popup si showPopup est true */}
        <Popup 
        showPopup={showPopup} // La proprit 'showPopup' contrle l'affichage de la popup en fonction de la valeur de 'showPopup'. 
        onClose={() => setShowPopup(false)} /> {/* Dfinit la variable showPopup  false, pour masquer la popup (ferm)*/}
      </form>
    </div>
  );
}

