
import React, { useState } from 'react';// Importe React et la fonction useState depuis la bibliothque React.
import { Tabs, Tab, Button } from 'react-bootstrap'; // Importe les composants Tabs, Tab et Button depuis React-Bootstrap.
import ErrorMsg from './ErrorMsg'; // Importe ErrorMsg depuis le fichier ErrorMsg.js


// composant fonctionnel appel 'Projet'.
const Projet = () => {
  // Utilisation du hook useState pour grer l'tat du composant
  const [activeTab, setActiveTab] = useState(0);  // // Dclare une variable d'tat 'activeTab' avec une valeur initiale de 0. Cette variable sera utilise pour suivre l'onglet actif slectionn dans le composant.
  const [selectedImages, setSelectedImages] = useState([]); // Dclare une variable d'tat 'selectedImages' avec une valeur initiale d'un tableau vide. Cette variable sera utilise pour stocker les images slectionnes dans le projet.
  const [autreTexte, setAutreTexte] = useState(''); // Dclare une variable d'tat 'autreTexte' avec une valeur initiale d'une chane vide. Cette variable peut tre utilise pour stocker un texte supplmentaire li au projet. 
  const [project_description, setProjectDescription] = useState(''); // Dclare une variable d'tat 'projectDescription' avec une valeur initiale d'une chane vide. Cette variable peut tre utilise pour stocker une description du projet. 
  const [project_type, setProjectType] = useState('');// Dclarez la variable d'tat projectType et le setter setProjectType dans votre composant
  const [services_requests, setServicesRequests] = useState([]);


  // Fonction pour grer le changement d'onglet (0,1,2)
  const handleTabSelect = (index) => { // Fonction handleTabSelect avec un paramtre 'index'
    setActiveTab(index); // Met  jour la valeur de l'onglet actif en utilisant la valeur 'index'
  };


  // Onglet 1 : fonction pour grer le clic sur une image carre 
const handleImageClick = (index) => {
  // Vrifie si l'image est dj slectionne
  const isSelected = selectedImages.includes(index);
  if (isSelected) {
    // Si l'image est dj slectionne, la dslectionne
    setSelectedImages(selectedImages.filter((item) => item !== index));
  } else {
    // Si l'image n'est pas slectionne, l'ajoute  la liste des slections
    setSelectedImages([...selectedImages, index]);
  }

  // Mettre  jour project_type en fonction de la slection de l'image carre dans l'onglet 1
  if (index === 0) {
    // Si l'image slectionne a un index de 0, dfinir le type de projet comme "Un Site Vitrine"
    setProjectType("Un Site Vitrine");
  } else if (index === 1) {
    // Si l'image slectionne a un index de 1, dfinir le type de projet comme "Un Site E-commerce"
    setProjectType("Un Site E-commerce");
  } else if (index === 2) {
    // Si l'image slectionne a un index de 2, dfinir le type de projet comme "Autre"
    setProjectType("Autre");
  }
};


// Onglet 2 : cocher une case
const handleServiceRequestChange = (service) => {
  // Crez une copie de la liste des services demands
  const updatedServices = [...services_requests];

  // Trouvez l'indice du service dans la liste
  const serviceIndex = updatedServices.indexOf(service);

  if (serviceIndex !== -1) {
    // Si le service est dj dans la liste, retirez-le
    updatedServices.splice(serviceIndex, 1);
  } else {
    // Sinon, ajoutez-le  la liste
    updatedServices.push(service);
  }

  // Mettez  jour l'tat des services demands
  setServicesRequests(updatedServices);
};


  // Vrifier si le bouton 'Suivant' doit tre dsactiv (l'onglet 3)
  const isNextButtonDisabled = (selectedImages.length === 0 && !autreTexte) || activeTab === 2;

// Fonction pour soumettre le formulaire
const handleSubmit = (e) => {
  e.preventDefault(); // Empche la soumission du formulaire par dfaut

  // Cre un objet formData avec les valeurs du formulaire
  const formData = {
    project_description, // Rcupre la description du projet
    project_type, // Rcupre le type de projet
    services_requests, // Rcupre les demandes de services
    selectedImages, // Rcupre les images slectionnes
  };

  // Rinitialisez les valeurs du formulaire pour les vider
  setProjectDescription(''); // Rinitialise la description du projet
  setProjectType(''); // Rinitialise le type de projet
  setServicesRequests([]); // Rinitialise les demandes de services
  setSelectedImages([]); // Rinitialise galement les slections des carrs dans le premier onglet
};


  // Rendu du composant 'Projet'
  return (
    <div className="backProjet">
      <h1 className="h1Projet">Un projet ?</h1>
      <p className="paraProjet">Dterminons votre besoin avec le formulaire ci-dessous</p>
      {/* Composant 'Tabs' pour grer les onglets */}
      <Tabs activeKey={activeTab} onSelect={handleTabSelect} id="devis-tabs" className="blockInfo">
        {/* Onglet 1 */}
        <Tab eventKey={0} title="Informations sur votre projet"> {/*Dfinit un onglet avec une cl unique (eventKey) gale  0*/}
          {/* Contenu de l'onglet 1 */}
          <p className="paraDemande">Votre demande concerne : <span style={{ color: "red" }}>*</span></p>
          {/* Slection des images carres */}
          <div className="containerSquare">
            <div className={`square ${selectedImages.includes(0) ? 'selected' : ''}`} onClick={() => handleImageClick(0)}>
              <img className='imgSquare' src="https://oguzdonmez.com/wp-content/uploads/2021/01/website.jpg" title="Un Site Vitrine" />
              {selectedImages.includes(0) && <span className="checkMark"></span>}
              <p className="paraSite1">Un Site Vitrine</p>
            </div>
            <div className={`square ${selectedImages.includes(1) ? 'selected' : ''}`} onClick={() => handleImageClick(1)}>
              <img className='imgSquare' src="https://oguzdonmez.com/wp-content/uploads/2021/04/magasin-carte-credit-coffrets-cadeaux-illustration-acheteurs_1262-18980-1.jpg" title="Un Site e-commerce" />
              {selectedImages.includes(1) && <span className="checkMark"></span>}
              <p className="paraSite2">Un Site E-commerce</p>
            </div>
            <div className={`square ${selectedImages.includes(2) ? 'selected' : ''}`} onClick={() => handleImageClick(2)}>
              <img className='imgSquare' src="https://oguzdonmez.com/wp-content/uploads/2021/04/unnamed.png" title="Autre" />
              {selectedImages.includes(2) && <span className="checkMark"></span>}
              <p className="paraSite3">Autre</p>
            </div>
          </div>
          {/* Champ de texte pour spcifier si 'Autre' est slectionn */}
          {selectedImages.includes(2) && (  // Vrifie si l'image avec l'index 2 est slectionne
           <div>
              <p className='projetPara'>Veuillez prciser la nature de votre site :</p>
              <div className='champText'>
                <input
                  type="text"
                  value={autreTexte}
                  onChange={(e) => setAutreTexte(e.target.value)} // Lorsque champ de texte change, met  jour l'tat 'autreTexte'
                  placeholder="Si vous ne savez pas exactement, expliquez votre site en 1 phrase."
                  style={{ height: '50%', width: '50%' }}
                />
              </div>
            </div>
          )}
        </Tab>
        {/* Onglet 2 */}
        <Tab eventKey={1} title="Informations sur votre demande">{/*Dfinit un onglet avec une cl unique (eventKey) gale  1*/}
          {/* Contenu de l'onglet 2 */}
          <h3 style={{ padding: "10px" }}>
            Vous souhaitez :<span style={{ color: "red" }}>*</span>
          </h3>
          {/* Choix des services demands avec des cases  cocher */}
          <label style={{ padding: "10px" }}>
            <input
              type="checkbox"
              name="service"
              value="creation-site-internet"
              onChange={() => handleServiceRequestChange("creation-site-internet")}
            />
            <span style={{ marginLeft: "10px" }}>Crer votre site internet</span>
          </label>
          <br />
          <label style={{ padding: "10px" }}>
            <input
              type="checkbox"
              name="service"
              value="refonte-site-existant"
              onChange={() => handleServiceRequestChange("refonte-site-existant")}
            />
            <span style={{ marginLeft: "10px" }}>Faire une refonte d'un site existant</span>
          </label>
          <br />
          <label style={{ padding: "10px" }}>
            <input
              type="checkbox"
              name="service"
              value="ameliorer-site"
              onChange={() => handleServiceRequestChange("ameliorer-site")}
            />
            <span style={{ marginLeft: "10px" }}>Amliorer votre site</span>
          </label>
          <br />
          <label style={{ padding: "10px" }}>
            <input
              type="checkbox"
              name="service"
              value="maintenance-site"
              onChange={() => handleServiceRequestChange("maintenance-site")}
            />
            <span style={{ marginLeft: "10px" }}>Une maintenance de votre site</span>
          </label>
          <br />
          <label style={{ padding: "10px" }}>
            <input
              type="checkbox"
              name="service"
              value="migration-site"
              onChange={() => handleServiceRequestChange("migration-site")}
            />
            <span style={{ marginLeft: "10px" }}>Une migration de votre site</span>
          </label>
          <br />
          <label style={{ padding: "10px" }}>
            <input
              type="checkbox"
              name="service"
              value="logo-site"
              onChange={() => handleServiceRequestChange("logo-site")}
            />
            <span style={{ marginLeft: "10px" }}>Un logo pour votre site</span>
          </label>
          <br />
          <p style={{ padding: "20px", textAlign: "left", color: "green" }}>
            Vous pouvez choisir plusieurs options si vous le souhaitez.
          </p>
        </Tab>
        {/* Onglet 3 */}
        <Tab eventKey={2} title="Informations sur vous">{/*Dfinit un onglet avec une cl unique (eventKey) gale  2*/}
          {/* Contenu de l'onglet 3 */}
           {/* Cre une instance "ErrorMsg" en lui passant la valeur "project_type, services".Ensuite l'utilise  l'intrieur "ErrorMsg" */}
          <ErrorMsg projectType={project_type} servicesRequests={services_requests} /> 
        </Tab>
      </Tabs>
      {/* Boutons pour naviguer entre les onglets */}
      <div className="buttonProjet">
        {/* onglet prcdent */}
        {/* activeTab est gal  0. Je clique sur ce bouton, l'onglet active est rduit de 1,permet de passer  l'onglet prcdent.*/}
        <Button disabled={activeTab === 0} onClick={() => setActiveTab((prev) => prev - 1)}>
          Prcdent
        </Button>
        {/* onglet suivant */}
        {/* Bouton est dsactiv en fonction de la valeur de isNextButtonDisabled.Je clique sur ce bouton, l'onglet active est augment de 1, ce qui permet de passer  l'onglet suivant.*/}
        <Button disabled={isNextButtonDisabled} onClick={() => setActiveTab((prev) => prev + 1)}>
          Suivant
        </Button>
      </div>
    </div>
  );
};

// Export du composant 'Projet' par dfaut
export default Projet;