import React from 'react';

// Composant Popup fonctionnel prenant deux proprits : showPopup et onClose.
const Popup = ({ showPopup, onClose }) => {
  // Affiche le contenu du composant uniquement si showPopup est vrai.
  return (
    showPopup && (
      <div className="popup">
        <div className="popup-content">
          <p>Le formulaire a bien t envoy.</p>
          {/* Un bouton pour fermer le Popup, qui appelle la fonction onClose lorsqu'il est cliqu. */}
          <button onClick={onClose}>Fermer</button>
        </div>
      </div>
    )
  );
};

// Exporte le composant Popup pour qu'il puisse tre utilis dans d'autres parties de l'application.
export default Popup;