import React from 'react'; // Importation du module React depuis la bibliothque React

// Importation du composant FontAwesomeIcon depuis le package '@fortawesome/react-fontawesome'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
// Importation des icnes spcifiques depuis le package '@fortawesome/free-solid-svg-icons'
import { faLaptop, faShoppingCart, faPuzzlePiece, faBlog, faSearch, faTools, faArrowRight } from '@fortawesome/free-solid-svg-icons';


// Dfinition d'un composant fonctionnel React nomm "Block" qui accepte trois proprits (props) : icon, title, et paragraph
const Block = ({ icon, title, paragraph }) => {
  return (
    <div className="block">
      <div className="icon">{icon}</div> {/* Affiche l'icne passe en tant que proprit "icon" */}
      <h3 className="title">{title}</h3> {/* Affiche le titre pass en tant que proprit "title" */}
      <p className="paragraph">{paragraph}</p> {/* Affiche le paragraphe pass en tant que proprit "paragraph" */}
      <FontAwesomeIcon icon={faArrowRight} size="m" /> {/* Flche droite de taille "m" */}
    </div>
  );
};


const Services = () => {
  // Tableau d'objets reprsentant les services, avec des icnes, titres et paragraphes
  const blocks = [
    {
      icon: <FontAwesomeIcon icon={faLaptop} />, // Icne d'un ordinateur portable
      title: 'Site Vitrine', 
      paragraph: 'Prsentez votre activit  travers un site internet moderne, propre et  votre image.', // Description du premier service
    },
    {
      icon: <FontAwesomeIcon icon={faShoppingCart} />, // Icne d'un panier d'achat
      title: 'Site E-Commerce', 
      paragraph: 'Une boutique en ligne cls en main pour vendre vos produits sur internet.', // Description du deuxime service
    },
    {
      icon: <FontAwesomeIcon icon={faPuzzlePiece} />, // Icne d'une pice de puzzle
      title: 'Application Web', 
      paragraph: 'Une application web pour interagir avec vos collaborateurs ou vos clients.', // Description du troisime service
    },
    {
      icon: <FontAwesomeIcon icon={faBlog} />, // Icne d'un blog
      title: 'Blog', 
      paragraph: 'Partagez vos connaissances et votre exprience avec vos lecteurs grce  un blog.', // Description du quatrime service
    },
    {
      icon: <FontAwesomeIcon icon={faSearch} />, // Icne de recherche
      title: 'Rfrencement - SEO', 
      paragraph: "Je vous accompagne dans l'amlioration de votre visibilit sur Google.", // Description du cinquime service
    },
    {
      icon: <FontAwesomeIcon icon={faTools} />, // Icne d'outils
      title: 'Maintenance WordPress', 
      paragraph: "Votre site WordPress a besoin d'une maintenance ? Laissez-moi faire !", // Description du sixime service
    },
  ];

  return (
    <div>
      <h1 className="classh1">MES SERVICES</h1> 
      <h2 className="classh2">MES PRINCIPAUX DOMAINES DE COMPTENCE</h2> 
      <div className="services"> 
      {/* Mapping de chaque bloc de service avec son index */}
        {blocks.map((block, index) => ( // map parcours tableau blocks, ainsi que son index associ
          <Block // blocks contient des lments de donnes 
            key={index} // Cl unique pour chaque bloc
            icon={block.icon} // Icne du bloc de service
            title={block.title} // Titre du bloc de service
            paragraph={block.paragraph} // Paragraphe de description du service 
          />
        ))}
      </div>
    </div>
  );
};

export default Services;
