// Importation de la bibliothque React
import React from 'react';

// Dfinition d'un tableau de projets avec des donnes de dmonstration
const projects = [
  {
    id: 1,
    title: 'Cabinet Dentaire Qwartz',
    description: 'Le cabinet dentaire Qwartz se trouve dans le centre commercial Qwartz  Villeuve-la-Garenne.',
    intervention: 'Je suis intervenu sur le site du cabinet afin de faire une refonte graphique. En effet, malgr la qualit du contenu, le site prsentait des bugs ou des lments casss.',
    type: 'Application web',
    image: 'images/dentaire.jpeg'
  },
  {
    id: 2,
    title: 'Fondation FACE',
    description: 'Le Concours Sengager pour les Quartiers a t initi par la Fondation FACE et lANRU en 2011.',
    intervention: 'Le Concours, divis en catgories, rcompense chaque anne des projets  vocation sociale, conomique et environnementale dvelopps dans des quartiers politique de la ville et, dans une moindre mesure, dans les territoires ruraux  fragiles .',
    type: 'Application web',
    image: 'images/CSPQ.jpeg'
  },
];

// Dfinition du composant Portfolio
function Portfolio() {
  return (
    <div>
      {projects.map(project => ( // Parcourt le tableau de projets et cre un lment pour chaque projet
        <div key={project.id} style={{ display: 'flex' }}>
          <div style={{marginRight: '20px', padding: '100px' }}>
            {/* Titre du projet */}
            <h2 className="project-title">{project.title}</h2>
            {/* Description du projet */}
            <p className="project-description">{project.description}</p>
            {/* Description de l'intervention sur le projet */}
            <p className="project-intervention">{project.intervention}</p>
          </div>
          <div>
            {/* Image du projet */}
            <img
              className="project-image"
              src={project.image}
              alt={project.title}
            />
          </div>
        </div>
      ))}
    </div>
  );
}

// Exportation du composant Portfolio pour qu'il puisse tre utilis ailleurs dans l'application
export default Portfolio;


